<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <h3 class="my-0">Tracking Codes</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          class="mr-3 my-0"
          @click="printPdf"
          type="primary"
          v-ripple.400
        >
          Print
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-3">
      <div class="box pt-3">
        <div class="row">
          <div class="col-12 flex-align-center">
            <p
              class="mx-0 table-tab"
              :class="filters.is_fulfilled && 'active-table-tab'"
              @click="filters.is_fulfilled = true"
            >
              Fulfilled
            </p>
            <p
              class="mx-0 table-tab"
              :class="!filters.is_fulfilled && 'active-table-tab'"
              @click="filters.is_fulfilled = false"
            >
              Not Fulfilled
            </p>
          </div>
          <div class="col-12"><div class="b-divider mb-3 mt-0"></div></div>
          <div class="col-12 flex-align-center">
            <p
              class="mx-0 table-tab"
              :class="filters.delivery === '' && 'active-table-tab'"
              @click="filters.delivery = ''"
            >
              All
            </p>
            <p
              v-for="delivery in deliveries"
              :key="delivery.id"
              class="mx-0 table-tab"
              :class="filters.delivery === delivery.id && 'active-table-tab'"
              @click="filters.delivery = delivery.id"
            >
              {{ delivery.name }}
            </p>
          </div>
          <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div>
          <div
            class="
              col-12
              mb-1
              d-flex
              justify-content-between
              align-items-center
            "
          >
            <div class="flex-align-center">
              <el-select
                v-model="filters.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 100"
                  :key="page"
                  :label="page * 100"
                  :value="page * 100"
                >
                </el-option>
              </el-select>
              <el-input
                v-model="filters.query"
                class="mr-2 table-search"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat no-width"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>

        <div class="w-100 el-main-table" v-loading="loading">
          <el-table ref="multipleTable" :data="data" style="width: 100%">
            <el-table-column width="50" label="ID">
              <template slot-scope="scope">
                {{ scope.row.id }}
              </template>
            </el-table-column>
            <el-table-column label="Code" width="250">
              <template slot-scope="scope">
                <img
                  @click="openLightBox(scope.row.image)"
                  :src="scope.row.image"
                  style="
                    width: 50px;
                    height: 50px;
                    object-fit: cover;
                    border-radius: 5px;
                    margin-right: 5px;
                  "
                  alt=""
                />
                {{ scope.row.link }}
              </template>
            </el-table-column>
            <el-table-column label="Status" width="150">
              <template slot-scope="scope">
                <el-tag
                  type="success"
                  class="rounded"
                  v-if="isFulfilled(scope.row)"
                  ><i class="bx bx-check-circle"></i> Fulfilled</el-tag
                >

                <el-tag type="danger" class="rounded" v-else
                  ><i class="bx bx-time-five"></i>Not Fulfilled</el-tag
                >
              </template>
            </el-table-column>
            <el-table-column label="Order ID">
              <template slot-scope="scope">
                {{ scope.row.sale }}
              </template>
            </el-table-column>
            <el-table-column label="Customer">
              <template slot-scope="scope">
                {{ scope.row.customer_name }}
              </template>
            </el-table-column>
            <el-table-column label="Delivery">
              <template slot-scope="scope">
                {{ scope.row.delivery }}
              </template>
            </el-table-column>
            <el-table-column label="Date">
              <template slot-scope="scope">
                {{ scope.row.created_at }}
              </template>
            </el-table-column>

            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-if="orders.total_pages !== 1">
            <el-pagination
              background
              @current-change="
                (index) => ((orders.current_page = index), getOrders())
              "
              :current-page="orders.current_page"
              layout="prev, pager, next"
              :page-count="orders.total_pages"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>

    <CoolLightBox
      :items="lightBoxFile"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      filters: {
        query: "",
        order_by: "-updated_at",
        pageSize: 10,
        supplier: "",
        date: "",
        is_fulfilled: false,
        delivery: "",
      },
      lightBoxFile: [],
      index: null,
      openFulfillDialog: false,
      openCreateMultipleDialog: false,
      editCodeDialog: false,
      creatingCode: false,
      creatingMultiple: false,
      updatingCode: false,
      loading: false,
      code: {
        code: "",
        app: "Pin Dou Dou",
        status: "Pending",
      },
      editingCode: {},
      actions: [
        {
          label: "Set as Received",
          value: "Received",
        },
        {
          label: "Set as Pending",
          value: "Pending",
        },
        {
          label: "Set as Cancelled",
          value: "Cancelled",
        },
        {
          label: "Delete",
          value: "Delete",
        },
      ],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
        {
          name: "Supplier A–Z",
          value: "supplier__name",
        },
        {
          name: "Supplier Z–A",
          value: "-supplier__name",
        },
      ],
      suppliers: [],
      selectedDate: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      data: [],
      orders: {
        current_page: 1,
        results: [],
        total_pages: 0,
      },
      cargos: [],
      loadingCargos: false,
      deletingCode: false,
      updatingMultiple: false,
      deliveries: [],
    };
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  components: {
    empty,
  },
  created() {
    this.$store.commit("setIsReady", false);
    this.getDelivery();
    this.getOrders();
  },
  watch: {
    "orders.current_page": {
      handler: function () {
        this.getOrders();
      },
      deep: true,
    },
    filters: {
      handler: function () {
        this.getOrders();
      },
      deep: true,
    },
  },
  methods: {
    isFulfilled(product) {
      return (
        parseFloat(product.quantity) ===
        parseFloat(product.number_of_fullfilled)
      );
    },
    openLightBox(image) {
      this.lightBoxFile = [image];
      this.index = 0;
    },
    printPdf() {
      var tableRows = "";
      this.data.forEach((order, index) => {
        tableRows += `<tr>
                          <td>${index + 1}</td>
                          <td>${order.link}</td>
                          <td>${order.created_at}</td>
                      </tr>`;
      });
      var html = `
                <!DOCTYPE html>
                <html lang="en">
                  <head>
                    <meta charset="UTF-8" />
                    <meta http-equiv="X-UA-Compatible" content="IE=edge" />
                    <meta name="viewport" content="width=device-width, initial-scale=1.0" />
                    <title>Order Codes</title>
                    <style>
                      table {
                        font-family: arial, sans-serif;
                        border-collapse: collapse;
                        width: 100%;
                        margin-bottom: 15px;
                      }

                      td,
                      th {
                        border: 1px solid #dddddd;
                        text-align: left;
                        padding: 8px;
                      }
                    </style>
                  </head>
                  <body>
                    <table>
                      <thead>
                        <tr>
                          <th style="width:50px;">No.</th>
                          <th>Code</th>
                          <th>Date</th>
                        </tr>
                      </thead>
                      <tbody id="table-body">${tableRows}</tbody>
                    </table>
                  </body>
                </html>
`;
      var newWin = window.open("", "Print-Window");
      newWin.document.open();
      newWin.document.write(html);
      newWin.window.print();
    },
    getOrders() {
      this.loading = true;
      this.gettingOrder = true;
      if (this.filters.date === null) {
        this.filters.date = "";
      }
      var url = `sale-products/?page=${this.orders.current_page}&page_size=${this.filters.pageSize}&query=${this.filters.query}&is_fulfilled=${this.filters.is_fulfilled}&order_by=${this.filters.order_by}&delivery=${this.filters.delivery}`;
      this.$axios
        .get(url)
        .then((response) => {
          this.loading = false;
          this.gettingOrder = false;
          var data = [];
          response.data.results.forEach((element) => {
            if (element.link) {
              var check = data.filter((da) => da.link == element.link);
              if (check.length == 0) {
                data.push(element);
              }
            }
          });
          this.data = data;
          this.orders.total_pages = response.data.total_pages;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.loading = false;
          this.gettingOrder = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    getDelivery() {
      var url = `all-deliveries/`;
      this.$axios
        .get(url)
        .then((response) => {
          this.deliveries = response.data;
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
  },
};
</script>

